import React, { useEffect, useState } from "react";
import axios from "../axios";
import { useNavigate } from "react-router-dom";
import { animations } from "../App";
import { motion } from "framer-motion";

const Admin = () => {
  const navigate = useNavigate();

  !localStorage.getItem("admin") && navigate("/");

  const [products, setProducts] = useState([]);
  const [navHeight, setNavHeight] = useState();
  const getProducts = async () => {
    const res = await axios.get("/products");
    setProducts(res.data);
  };
  useEffect(() => {
    getProducts();
    setNavHeight(document.querySelector(".navbar").offsetHeight);
  }, []);

  const [loading, setLoading] = useState(false);

  const [insert, setInsert] = useState({
    product_img: "",
    title: "",
    material: "",
    price: "",
  });
  const [update, setUpdate] = useState({
    existTitle: "",
    product_img: "",
    title: "",
    material: "",
    price: "",
  });
  const [deleteTitle, setDeleteTitle] = useState("");

  //insert product
  const handleImage = (e) => {
    const reader = new FileReader();
    reader.readAsDataURL(e.target.files[0]);
    reader.onloadend = () =>
      setInsert({ ...insert, product_img: reader.result });
  };
  const AddProduct = async (e) => {
    e.preventDefault();
    setLoading(true);
    try {
      await axios.post("/insertProduct", insert);
      scrollTo(0, 0);
      alert("Product Added!");
    } catch (error) {
      alert(error.message);
      console.log(error);
    } finally {
      setLoading(false);
    }
  };

  //update product
  const handleUpdateImage = (e) => {
    const reader = new FileReader();
    reader.readAsDataURL(e.target.files[0]);
    reader.onloadend = () =>
      setUpdate({ ...update, product_img: reader.result });
  };
  const UpdateProduct = async (e) => {
    e.preventDefault();
    setLoading(true);

    try {
      await axios.post("/updateProduct", update);
      scrollTo(0, 0);
      alert("Product Updated!");
    } catch (error) {
      alert(error.message);
      console.log(error);
    } finally {
      setLoading(false);
    }
  };

  //delete Product
  const DeleteProduct = async (e) => {
    e.preventDefault();
    setLoading(true);
    try {
      await axios.get(`/deleteProduct/${deleteTitle}`);
      scrollTo(0, 0);
      alert("Product Removed!");
    } catch (error) {
      alert(error.message);
      console.log(error);
    } finally {
      setLoading(false);
    }
  };

  return (
    <motion.div {...animations} className="container my-3">
      <datalist id="list">
        {products.map((all) => (
          <option key={all._id}>{all.title}</option>
        ))}
      </datalist>
      {/* loading screen */}
      {loading && (
        <div
          style={{
            height: innerHeight - navHeight,
          }}
          className="container-fluid loading_screen"
        >
          <h1>Loading</h1>
        </div>
      )}
      <div className="row g-5">
        <div className="col-md-4 col-12">
          <h1 className="text-center">Add Product</h1>
          <form onSubmit={AddProduct}>
            <input
              onChange={handleImage}
              type="file"
              className="form-control mb-3"
              required
              accept=".png,.jpg"
            />
            <input
              onChange={(e) => setInsert({ ...insert, title: e.target.value })}
              type="text"
              className="form-control mb-3"
              required
              placeholder="Title"
            />
            <input
              onChange={(e) =>
                setInsert({ ...insert, material: e.target.value })
              }
              type="text"
              className="form-control mb-3"
              required
              placeholder="Material"
            />
            <input
              onChange={(e) => setInsert({ ...insert, price: e.target.value })}
              type="text"
              className="form-control mb-3"
              required
              placeholder="Price"
            />
            <button type="submit" className="btn btn-success">
              Insert Product
            </button>
          </form>
        </div>

        {/* update */}
        <div className="col-md-4 col-12">
          <h1 className="text-center">Update Product</h1>
          <form onSubmit={UpdateProduct}>
            <input
              list="list"
              type="text"
              className="form-control mb-3"
              required
              placeholder="Old Title"
              onChange={(e) =>
                setUpdate({ ...update, existTitle: e.target.value })
              }
              value={update.existTitle}
            />
            <input
              type="file"
              className="form-control mb-3"
              accept=".png,.jpg"
              onChange={handleUpdateImage}
            />
            <input
              type="text"
              className="form-control mb-3"
              placeholder="New Title"
              onChange={(e) => setUpdate({ ...update, title: e.target.value })}
              value={update.title}
            />
            <input
              type="text"
              className="form-control mb-3"
              placeholder="New Material"
              onChange={(e) =>
                setUpdate({ ...update, material: e.target.value })
              }
              value={update.material}
            />
            <input
              type="text"
              className="form-control mb-3"
              placeholder="New Price"
              onChange={(e) => setUpdate({ ...update, price: e.target.value })}
              value={update.price}
            />
            <button type="submit" className="btn btn-primary">
              Update Product
            </button>
          </form>
        </div>

        {/* delete */}
        <div className="col-md-4 col-12">
          <h1 className="text-center">Delete Product</h1>
          <form onSubmit={DeleteProduct}>
            <input
              list="list"
              placeholder="Title"
              type="text"
              required
              className="form-control mb-3"
              value={deleteTitle}
              onChange={(e) => setDeleteTitle(e.target.value)}
            />
            <button type="submit" className="btn btn-danger">
              Delete Product
            </button>
          </form>
        </div>
      </div>
    </motion.div>
  );
};

export default Admin;
