import React from "react";
import { Link } from "react-router-dom";

const Footer = () => {
  const admin = localStorage.getItem("admin");
  return (
    <footer className="py-5 px-md-5 px-2 bg-primary text-light">
      <div className="row m-0 g-5">
        <div className="col-md-3 col-12">
          <h3>MR Bandhani</h3>
          Here you can use rows and columns to organize your footer content.
          Lorem ipsum dolor sit amet, consectetur adipisicing eli.
        </div>
        <div className="col-md-3 col-12">
          <h3>Products</h3>
          Here you can use rows and columns to organize your footer content.
          Lorem ipsum dolor sit amet, consectetur adipisicing eli.
        </div>
        <div className="col-md-3 col-12">
          <h3>Useful Links</h3>
          Here you can use rows and columns to organize your footer content.
          Lorem ipsum dolor sit amet, consectetur adipisicing eli.
        </div>

        <div className="col-md-3 col-12">
          <h3>Contact</h3>
          Here you can use rows and columns to organize your footer content.
          Lorem ipsum dolor sit amet, consectetur adipisicing eli.
        </div>
      </div>
      <hr className="mx-auto hr_line" />

      <div
        className="
          d-flex
          flex-md-row flex-column
          justify-content-between
          align-items-center
          text-center
        "
      >
        <p>
          <span
            onClick={() => {
              const get = prompt("Password: ");
              if (get === "mrbandhani12345") {
                localStorage.setItem("admin", true);
              } else if (get === "mrbandhaniremove") {
                localStorage.removeItem("admin");
              } else {
                alert("You Are not Admin!");
              }
            }}
          >
            Â© 2021 Copyright:
          </span>{" "}
          MRBandhani.com
        </p>
        <div className="d-flex gap-3 icons" style={{ fontSize: "2rem" }}>
          <i className="fab fa-facebook"></i>
          <i className="fab fa-whatsapp"></i>
          <i className="fab fa-instagram"></i>
          <i className="fab fa-telegram"></i>
        </div>
      </div>
      {admin && (
        <p className="text-center mt-3 mb-0" onClick={() => scrollTo(0, 0)}>
          <Link className="text-reset" to="/admin">
            Admin
          </Link>
        </p>
      )}
    </footer>
  );
};

export default Footer;
