import React, { useEffect, useState } from "react";

import { motion } from "framer-motion";
import { animations } from "../App";

const Home = () => {
  const [navHeight, setNavHeight] = useState(0);

  useEffect(() => setNavHeight(document.querySelector("nav").offsetHeight), []);

  return (
    <motion.div {...animations} className="container home_screen">
      <div
        style={{ height: innerHeight - navHeight }}
        className="d-flex flex-column justify-content-center align-items-center"
      >
        <motion.h1
          initial={{
            opacity: 0,
            scale: 1.3,
          }}
          animate={{
            opacity: 1,
            scale: 1,
          }}
          transition={{
            duration: 0.7,
            ease: "anticipate",
          }}
          className="header_text"
        >
          MR Bandhani
        </motion.h1>
        <motion.p
          initial={{
            opacity: 0,
          }}
          animate={{
            opacity: 1,
          }}
          transition={{
            delay: 0.7,
            duration: 0.5,
            ease: "anticipate",
          }}
        >
          Exclusive Bandhani Showroom
        </motion.p>
      </div>
    </motion.div>
  );
};

export default Home;
