import { motion } from "framer-motion";
import React from "react";
import { Link } from "react-router-dom";

const Product = ({ i, _id, product_img, title, material, price }) => {
  return (
    <motion.div
      transition={{
        delay: i * 0.3,
        duration: 0.5,
      }}
      initial={{
        opacity: 0,
        y: 10,
      }}
      animate={{
        opacity: 1,
        y: 0,
      }}
    >
      <div className="col-12 d-flex flex-row justify-content-center align-items-center gap-2 text-capitalize">
        {/* <Link to={`/products/${_id}`}> */}
        <img src={product_img} alt={title} className="product_img" />
        {/* </Link> */}
        <div className="card-body">
          <div className="card-title fw-bolder fs-4">{title}</div>
          <p className="mb-0">Material: {material}</p>
          <p>Price: Rs.{price}</p>
        </div>
      </div>
      <hr className="mx-auto my-3 product_divider" />
    </motion.div>
  );
};

export default Product;
